import React from 'react';
import { Card, CardHeader, CardBody, CardTitle } from '../../components/UI/Card';

const CourseDetail = () => {
  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-3xl font-bold text-gray-900">Course Details</h1>
        <p className="text-gray-600">View detailed information about this course</p>
      </div>

      <Card>
        <CardHeader>
          <CardTitle>Course Information</CardTitle>
        </CardHeader>
        <CardBody>
          <div className="text-center py-12">
            <div className="text-6xl mb-4">🎓</div>
            <h3 className="text-xl font-semibold text-gray-900 mb-2">Course Details Coming Soon</h3>
            <p className="text-gray-600">
              Detailed course information, syllabus, assignments, and enrollment options 
              will be available here.
            </p>
          </div>
        </CardBody>
      </Card>
    </div>
  );
};

export default CourseDetail;
